<template>
<div id="login-popup">
  <div class="black-bg" @click="closeLoginPopup">
  </div>
  <form class="login-popup-content" @submit.prevent="onSubmit">
      <div class="login-form-hide">
        <img class="hide-btn" @click="closeLoginPopup" src="../../assets/btn_close.svg" alt="">
      </div>
      <h4 class="popup-title">{{popup.popupTitle}}</h4>
      <div class="form-container">
        <p class="guide-to-login">{{popup.popupInfo[popup.index]}}</p>
        <div class="login-form">
          <div class="user-inform">
            <!--  -->
            <input class="form-value" :type="formFirst.type" v-model="formFirst.value" :placeholder="placeHolder[0]"> 
            <div class="login-guide" v-if="idGuideDisplay == 1">{{placeHolder[0]}} .</div>
          </div>
          <div class="user-inform">
            <!--  -->
            <input class="form-value" :type="formSecond.type" v-model="formSecond.value" :placeholder="placeHolder[1]">
            <div class="login-guide" v-if="passwordGuideDisplay == 1">{{placeHolder[1]}} .</div>
          </div>
        </div>
        <!-- <p id="find-user-id" >    {{user.id}}.</p> -->
        <div class="signup-guide-container" v-if="popup.popupTitle == ''">
          <span class="guide-to-signup">  ?</span>
          <router-link to="/Signup" class="move-to-signup"></router-link>
        </div>
        <button type="submit"><img class="login-btn" v-if="popup.popupSort == 'login'" src="../../assets/btn_login.svg" alt="btn_login"></button>
        <button type="submit"><img class="login-btn" v-if="popup.popupSort == 'id' || popup.popupSort == 'password'" src="../../assets/btn_find.svg" alt="btn_login"></button>
        <div class="find-container">
          <span @click="changeFindId" v-if="popup.popupSort == 'password' || popup.popupSort == 'login'"> </span>
          <span v-if="popup.popupSort == 'login'"> | </span>
          <span @click="changeFindPassword" v-if="popup.popupSort == 'id' || popup.popupSort == 'login'"> </span>
          <span v-if="popup.popupSort == 'id' || popup.popupSort == 'password'"> | </span>
          <span @click="backLogin" v-if="popup.popupSort == 'id' || popup.popupSort == 'password'"></span>
        </div>
      </div>  

  </form>
</div>
</template>

<script>
import axios from "axios"

export default {
    name : 'login-popup',
    props : ['_loginPopupState', '_loginState', '_userName'],
    data() {
      return {
        user : {
          id : '',
          name : ''
        },
        popup : {
          index : 0, //0 : , 1 : html, 2 : css : 3
          popupTitle : '',
          popupSort : 'login',
          popupInfo : ['   html, css, javascript    .', '   Find     .', '   Find    .'],
        },
        //state
        loginState : 0, //0    , 1   
        loginPopupState : this._loginPopupState,
        // form  data
        placeHolder : ["", ""],
        formFirst : {
          type : '',
          value : '',
        },
        formSecond : {
          type : 'password',
          value : ''
        },
        //   &   
        idGuideDisplay : 0, //1     
        passwordGuideDisplay : 0, //1     
        nullcheck : 0, //0 : , , 1 : ,   (  )
      }
    },
    created() {
      this.loginState = this._loginState;
    },
    methods : {
      closeLoginPopup() {
        console.log("  ");
        this.loginPopupState = 0;
        this.$emit('_loginClose');
      },
      formNullCheck() {
        this.nullcheck = 0 // nullcheck true ,       1   
        if (this.formFirst.value == '' && this.formSecond.value == '') {
          this.idGuideDisplay = 1;
          this.passwordGuideDisplay = 1;
        } else if(this.formFirst.value == '') {
          this.idGuideDisplay = 1;
          this.passwordGuideDisplay = 0;
        } else if(this.formSecond.value == '') {
          this.idGuideDisplay = 0;
          this.passwordGuideDisplay = 1;
        } else { // ,     
          console.log(",   ");
          this.idGuideDisplay = 0;
          this.passwordGuideDisplay = 0;
          this.nullcheck = 1
        }
      },
      onSubmit() {
        this.formNullCheck(); //     
        if (this.popup.index === 0) {
          this.tryLogin();
        } else if (this.popup.index === 1) {
          this.findId();
        } else {
          this.findPass();
        }
      },
      formInit() {
        document.getElementsByClassName("form-value").value = "";
        document.getElementsByClassName("form-value").value = "";
        this.formFirst.value = '';
        this.formSecond.value = '';
      },
      tryLogin() {
        if (this.nullcheck == 1) {
          axios
          .post('http://3.36.131.138/api/login', {
            userId : this.formFirst.value,
            password : this.formSecond.value
          })
          .then(res => { // 
            if(res.data){
              this.closeLoginPopup();
              this.completeLogin();
            }else{
              alert("   .");
              this.formInit(); //form  
            }
          })
          .catch(err => {
              console.log(err.response);
          })
        }
      },
      completeLogin() { // 
        this.loginState = 1
        this.userInfo();
      },
      userInfo() {
        console.log("userInfo  .");
        axios
        .get("http://3.36.131.138/memberInfo")
        .then(res => {
          console.log(res);
          localStorage.setItem('loginState', JSON.stringify(this.loginState));
          localStorage.setItem('userName', res.data.name + '');
          this.$emit('_completeLogin')
        })
        .catch(err => {
          console.log(err);
        })
      },
      findId() {
        if (this.nullcheck == 1) {          
          axios
          .post('http://3.36.131.138/api/idFind', {
            name : this.formSecond.value,
            email : this.formFirst.value,
          })
          .then(res => {
            console.log(res);
            this.user.id = res.data
            this.formInit(); //form  
            if (res.data != "") {
              alert("   " + this.user.id + ".")
            } else {
              alert("  .   .")
            }
          })
          .catch(err => {
            console.log(err);
          })
        }
      },
      findPass() {
        if (this.nullcheck == 1) {
          axios
          .put('http://3.36.131.138/api/passFind', {
            userId : this.formSecond.value,
            email : this.formFirst.value,
          })
          .then(res => {
            console.log(res);
            if (res.data) {
              alert("   .")
              this.formInit(); //form  
            }else {
              alert("  .   .")
            }
          })
          .catch(err => {
            console.log(err);
          })
        }
      },

      // ,   
      changeFindId() {
        this.popup.index = 1
        this.popup.popupTitle = ' ';
        this.popup.popupSort = 'id'
        this.placeHolder = ['', '']
        this.formInit(); // form  
        this.formFirst.type = 'email'; //    
        this.formSecond.type = 'text'; //      
      },
      changeFindPassword() {
        this.popup.index = 2
        this.popup.popupTitle = ' ';
        this.popup.popupSort = 'password'
        this.placeHolder = ['', '']
        this.formInit(); // form  
        this.formFirst.type = 'email'; //    
        this.formSecond.type = 'text'; //      
      },
      backLogin() {
        this.popup.index = 0
        this.popup.popupTitle = '';
        this.popup.popupSort = 'login'
        this.placeHolder = ['', '']
        this.formInit(); // form  
        this.formFirst.type = 'text'; //    
        this.formSecond.type = 'password'; //    
      }
    }
  }
</script>

